import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { InterestRateMaintenance } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-maintenance/bean/interest-rate-maintenance.bean";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
@Component({
    selector: 'interest-define-add',
    templateUrl: './interest-define-add.component.html',
    styleUrls: ['./interest-define-add.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class InterestDefineAddComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    title: any;//页面标题
    data: any;//父页面传递的数据
    rateName: any;
    currencyUomId:any;
    OrgId: any =[];//机构
    currency: any=[];//币种
    interestratemaintenance: InterestRateMaintenance = new InterestRateMaintenance();
    constructor(
        public httpService: ValuationHttpService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {
    }

    ngOnInit() {
        this.OrgId = [{ label: '请选择', value: '' }, { label: '台州银行', value: 'tzbank' }, { label: '乡镇银行', value: 'xzbank' }];//机构
        this.title = this.route.snapshot.params['title'];
        this.data = this.route.snapshot.params['data'];
        this.rateName = this.route.snapshot.params['rateName'];
        this.currencyUomId = this.route.snapshot.params['currencyUomId'];
        this.interestratemaintenance.rateDefineId = this.route.snapshot.params['rateDefineId'];
        if (this.data && this.data != null) {
            this.selectPriceRatePurpose();
        }
        this.selectProductCondValueList();
        this.codeVal();
        //this.currency = this.code['CURRENCY'];//币种
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量

    }
     //币种码值
     selectProductCondValueList() {
        let params = {
            conditionalDimensionCode:'cyyCode'
        }
        this.httpService.selectProductCondValueList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                List.push({ "label": '请选择', "value": null });
                data.enumerationValueList.forEach((v) => {
                    List.push({ "label": v.dimensionValue, "value": v.dimensionCode });
                })
                this.currency = List;  

            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '查询失败', detail: '调用服务失败！' });
        })
    }
    //修改页面数据初始化
    selectPriceRatePurpose() {
        let params = {
            ratePurposeId: this.data,//利率Id
        }
        this.httpService.selectPriceRatePurpose(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.interestratemaintenance = data;
                this.interestratemaintenance.ratePurposeId = this.data;
                this.interestratemaintenance.rateDefineId = this.route.snapshot.params['rateDefineId'];
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '利率定义查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '利率定义查询失败', detail: '调用服务失败！' });
        })
    }
    save() {
        //期限不能为空
        if (!this.interestratemaintenance.rateTerm) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '期限不能为空' });
            return;
        }
        //利率值不能为空
        if (!this.interestratemaintenance.rateValueCode) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率值不能为空' });
            return;
        }
        //生效日期不能为空
        if (!this.interestratemaintenance.fromDate) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '生效日期不能为空' });
            return;
        }
        if (this.title == '利率定义值新增') {
            this.createPriceRatePurpose();
        } else if (this.title == '利率定义值修改') {
            this.updatePriceRatePurpose();
        }
    }
    //存款基础利率值新增
    createPriceRatePurpose() {
        let param = {
            rateDefineId: this.interestratemaintenance.rateDefineId, //利率定义ID
            rateValueCode: this.interestratemaintenance.rateValueCode, //	基准利率值
            fromDate: this.commfunc.transDateN(this.interestratemaintenance.fromDate), //	生效日期
            thruDate: this.commfunc.transDateN(this.interestratemaintenance.thruDate), //	失效日期
            branchCode: this.interestratemaintenance.branchCode, //	分行代码
            rateOrgId: this.interestratemaintenance.rateOrgId, //	机构号
            rateTerm: this.interestratemaintenance.rateTerm, //期限
            rateStatus: this.interestratemaintenance.rateStatus, //	利率状态
        }
        this.httpService.createPriceRatePurpose(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '基础利率值新增成功', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基础利率值新增失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基础利率值新增失败', detail: '调用服务失败！' });
        })
    }
    //存款基础利率值修改
    updatePriceRatePurpose() {
        let param = {
            ratePurposeId: this.interestratemaintenance.ratePurposeId, //利率ID
            rateValueCode: this.interestratemaintenance.rateValueCode, //	基准利率值
            fromDate: this.commfunc.transDateN(this.interestratemaintenance.fromDate), //	生效日期
            thruDate: this.commfunc.transDateN(this.interestratemaintenance.thruDate), //	失效日期
            branchCode: this.interestratemaintenance.branchCode, //	分行代码
            rateOrgId: this.interestratemaintenance.rateOrgId, //	机构号
            rateTerm: this.interestratemaintenance.rateTerm, //期限
            rateStatus: this.interestratemaintenance.rateStatus, //	利率状态
        }
        this.httpService.updatePriceRatePurpose(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '基础利率值修改成功', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基础利率值修改失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基础利率值修改失败', detail: '调用服务失败！' });
        })
    }
    reset() {
        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-define-detail',{backrateName: this.rateName,backcurrencyUomId:this.currencyUomId, backrateDefineId:this.interestratemaintenance.rateDefineId}])
    }
}
