import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { InterestRateMaintenance } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-maintenance/bean/interest-rate-maintenance.bean";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { specialSymbols,checkChinese, checkEnAndNum } from "app/@uisftech/common/service/validator";
@Component({
    selector: 'interest-rate-mainadd',
    templateUrl: './interest-rate-mainadd.component.html',
    styleUrls: ['./interest-rate-mainadd.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class InterestRateMainaddComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    display: boolean = false;
    title: any;//页面标题
    data: any;//父页面传递的数据
    backdata: any;//返回该页面存储的数据
    listOptions: any = [];//利率类别
    interestform: any = [];//利率类型
    interestType: any = [];//利率种类
    EnAndNumShow: Boolean = false;//利率编号校验
    ChineseShow: Boolean = false;//利率名称校验
    isExistBaseRate: any;//是否有值数据
    interestratemaintenance: InterestRateMaintenance = new InterestRateMaintenance();
    constructor(
        public httpService: ValuationHttpService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {}

    ngOnInit() {
        this.listOptions = [{ label: '请选择', value: '' }, { label: '基础利率', value: '1' }, { label: '基准利率', value: '3' }, { label: '市场利率', value: '4' }];//利率类别
        this.interestform = [{ label: '请选择', value: '' }, { label: '标准', value: 'bor' }, { label: 'libor', value: 'libor' }, { label: 'hibor', value: 'hibor' }, { label: 'sibor', value: 'sibor' }];//利率类型
        this.interestType = [{ label: '请选择', value: '' }, { label: '存款活期利率', value: '01' }, { label: '存款定期利率', value: '02' }, { label: '贷款利率', value: '03' }];//利率种类
        this.title = this.route.snapshot.params['title'];
        this.data = this.route.snapshot.params['data'];
        //修改
        if (this.data && this.data != null) {
            this.selectPriceRateDefine();
        }
        this.codeVal();
        this.backdata = this.route.snapshot.params['backdata'];
        if (this.backdata && this.backdata != null) {
            debugger;
            if(this.route.snapshot.params['is']=='0'){
                this.isExistBaseRate = 0;
            } else if(this.route.snapshot.params['is']=='1'){
                this.isExistBaseRate = 1;
            }
            this.interestratemaintenance = JSON.parse(this.backdata); 
        }
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //修改页面数据初始化
    selectPriceRateDefine() {
        let params = {
            baseRateDefineId: this.data,//利率类型Id
        }
        this.httpService.selectPriceRateDefine(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.interestratemaintenance = data.productPriceRateDefine;
                this.isExistBaseRate = data.productPriceRateDefine.isExistBaseRate;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '利率定义查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '利率定义查询失败', detail: '调用服务失败！' });
        })
    }
    //汉字校验
    onChineseCheck() {
        if (this.interestratemaintenance.baseRateName != '') {
            if (specialSymbols(null, this.interestratemaintenance.baseRateName)) {
                this.ChineseShow = true;
            } else {
                this.ChineseShow = false;
            }
        } else {
            this.ChineseShow = false;
        }
    }
    //数字和英文校验
    onEnAndNumCheck() {
        if (this.interestratemaintenance.baseRateDefineId != '') {
            if (checkEnAndNum(null, this.interestratemaintenance.baseRateDefineId)) {
                this.EnAndNumShow = true;
            } else {
                this.EnAndNumShow = false;
            }
        } else {
            this.EnAndNumShow = false;
        }
    }
    //下一步
    save() {
        console.log(this.isExistBaseRate);
        //利率类别不能为空
        if (!this.interestratemaintenance.baseRateClassify) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率类别不能为空' });
            return;
        }
        //利率类型不能为空
        if (!this.interestratemaintenance.baseRateType) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率类型不能为空' });
            return;
        }
        //利率种类不能为空
        if (!this.interestratemaintenance.baseRateSort) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率种类不能为空' });
            return;
        }
        //利率编号不能为空
        if (!this.interestratemaintenance.baseRateDefineId) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率编号不能为空' });
            return;
        }
        //利率名称不能为空
        if (!this.interestratemaintenance.baseRateName) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率名称不能为空' });
            return;
        }
        if(this.EnAndNumShow || this.ChineseShow){
            return;
        }
        if (this.title == '利率定义新增') {
            let params = {
                baseRateDefineId: this.interestratemaintenance.baseRateDefineId,//	利率编号
            }
            this.httpService.selectPriceRateDefineList(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.productPriceRateDefineList.length != 0) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '利率编号已存在' });
                    } else {
                        let parames = {
                            baseRateName: this.interestratemaintenance.baseRateName,//	利率编号
                        }
                        this.httpService.selectPriceRateDefineByName(parames).subscribe(data => {
                            if (data.returnCode.code == 'success') {
                                if (data.totalNum == 0) {
                                    this.msgs = [];
                                    this.msgs.push({ severity: 'error', summary: '提示', detail: '利率名称已存在' });
                                } else {
                                    let data = JSON.stringify(this.interestratemaintenance);
                                    this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-define-detail', { title: this.title, data: data ,is: this.isExistBaseRate}])
                                }
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '基准利率名称查询失败', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '基准利率名称查询失败', detail: '调用服务失败！' });
                        })
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '基准利率查询失败', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基准利率查询失败', detail: '调用服务失败！' });
            })
        } else {
            let parames = {
                baseRateDefineId: this.interestratemaintenance.baseRateDefineId,//	利率编号
                baseRateName: this.interestratemaintenance.baseRateName,//	利率编号
            }
            this.httpService.selectPriceRateDefineByName(parames).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.totalNum == 0) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '利率名称已存在' });
                    } else {
                        let data = JSON.stringify(this.interestratemaintenance);
                        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-define-detail', { title: this.title, data: data, is: this.isExistBaseRate }])
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '基准利率名称查询失败', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基准利率名称查询失败', detail: '调用服务失败！' });
            })
        }
    }
    //取消
    reset() {
        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-maintenance', { query: '1' }])
    }
}
