import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { InterestRateTrialComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-trial/interest-rate-trial.component";
import { InterestRateTdetailComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-trial/interet-rate-tdetails/interet-rate-tdetails.component";
import { InterestRateMaintenanceComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-maintenance/interest-rate-maintenance.component";
import { InterestRateMainaddComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-maintenance/interest-rate-mainadd/interest-rate-mainadd.component";
import { InterestDefineDetailComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-maintenance/interest-define-detail/interest-define-detail.component";
import { InterestDefineAddComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-maintenance/interest-define-add/interest-define-add.component";

export const routes: Routes = [
    {
        path: '',

        children: [
            { path: '', redirectTo: 'interest-rate', pathMatch: 'full' },
            { path: 'interest-rate-trial', component: InterestRateTrialComponent },
            { path: 'interest-rate-tdetail', component: InterestRateTdetailComponent },
            { path: 'interest-rate-maintenance', component: InterestRateMaintenanceComponent },
            { path: 'interest-rate-mainadd', component: InterestRateMainaddComponent },
            { path: 'interest-define-detail', component: InterestDefineDetailComponent },
            { path: 'interest-define-add', component: InterestDefineAddComponent },
            { path: 'interest-rate-allocation',  loadChildren: './interest-rate-allocation/interest-rate-allocation.module#InterestRateAllocatModule' },
        ]
    },
]
export const InterestRateRouting: ModuleWithProviders = RouterModule.forChild(routes);