import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { InterestRateMaintenance } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-maintenance/bean/interest-rate-maintenance.bean";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { specialSymbols,checkEnAndNum } from "app/@uisftech/common/service/validator";
@Component({
    selector: 'interest-rate-maintenance',
    templateUrl: './interest-rate-maintenance.component.html',
    styleUrls: ['./interest-rate-maintenance.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class InterestRateMaintenanceComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    list_Table: any = [];//表格数据
    listOptions: any = [];//利率类别
    interestform: any = [];//利率类型
    interestType: any = [];//利率种类
    index: any = '0';//是否初始化查询
    EnAndNumShow: Boolean = false;//利率编号校验
    ChineseShow: Boolean = false;//利率名称校验
    interestratemaintenance: InterestRateMaintenance = new InterestRateMaintenance();
    constructor(
        public httpService: ValuationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {}

    ngOnInit() {
        this.codeVal();
        this.listOptions = [{ label: '请选择', value: '' },{ label: '基础利率', value: '1' }, { label: '基准利率', value: '3' }, { label: '市场利率', value: '4' }];//利率类别
        this.interestform = [{ label: '请选择', value: '' },{ label: '标准', value: 'bor' }, { label: 'libor', value: 'libor' }, { label: 'hibor', value: 'hibor' },{label: 'sibor', value: 'sibor'}];//利率类型
        this.interestType = [{ label: '请选择', value: '' }, { label: '存款活期利率', value: '01' }, { label: '存款定期利率', value: '02' }, { label: '贷款利率', value: '03' }];//利率种类
        this.index = this.route.snapshot.params['query'];
        if(this.index){
            this.query();
        }
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //查询
    query() {
        if(this.EnAndNumShow || this.ChineseShow){
            return;
        }
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.selectPriceRateDefineList();
    }
    selectPriceRateDefineList() {
        let params = {
            baseRateClassify: this.interestratemaintenance.baseRateClassify,//利率类别
            baseRateType: this.interestratemaintenance.baseRateType,//	利率类型
            baseRateSort: this.interestratemaintenance.baseRateSort,//	利率种类
            baseRateDefineId: this.interestratemaintenance.baseRateDefineId,//	利率编号
            baseRateName: this.interestratemaintenance.baseRateName,//	利率名称
            pageSize: this.pageSize,
            pageNum: this.pageNum,
        }
        this.httpService.selectPriceRateDefineList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.list_Table = data.productPriceRateDefineList;
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基准利率查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基准利率查询失败', detail: '调用服务失败！' });
        })
    }
    //翻页
    paginate(event) {
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = event.page * this.pageSize;
        this.selectPriceRateDefineList();
    }
    //重置
    reset() {
        this.interestratemaintenance = new InterestRateMaintenance();
        this.EnAndNumShow = false;
        this.ChineseShow = false;
        this.query();
    }
     //汉字校验
     onChineseCheck() {
        if (this.interestratemaintenance.baseRateName != '') {
            if (specialSymbols(null, this.interestratemaintenance.baseRateName)) {
                this.ChineseShow = true;
            } else {
                this.ChineseShow = false;
            }
        } else{
            this.ChineseShow = false;
        }
    }
    //数字和英文校验
    onEnAndNumCheck() {
        if (this.interestratemaintenance.baseRateDefineId != '') {
            if (checkEnAndNum(null, this.interestratemaintenance.baseRateDefineId)) {
                this.EnAndNumShow = true;
            } else {
                this.EnAndNumShow = false;
            }
        } else{
            this.EnAndNumShow = false;
        }
    }
    //新增
    addBtn() {
        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-mainadd',{ title: '利率定义新增'}])
    }
    //修改
    toUpdate(col) {
       this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-mainadd',{ title: '利率定义修改', data: col.baseRateDefineId }])
    }
    //详情
    toDetail(col) {
       this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-mainadd',{ title: '利率定义详情', data: col.baseRateDefineId }])
    }
    //删除
    toDelete(col) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                //条件
                let param = {
                    baseRateDefineId: col.baseRateDefineId,//利率编号
                };
                this.httpService.removePriceRateDefine(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.first = 0;
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                })
            }
        });
    }
}
